.container {
  color: rgb(56, 218, 56);
  margin: 1.2rem 0 2rem 0;
  text-align: center;
  font-size: 2.2rem;
}
.container-body {
  margin-left: 2rem;
  border: 1px solid black;
  width: fit-content;
  height: fit-content;
  padding: 1rem;
  border-radius: 1rem;
  font-size: 3rem;
}
input {
  font-size: 2.5rem;
  width: 2rem;
  text-align: center;
  margin: 0.4rem;
  width: fit-content;
  border-radius: 1rem;
  padding: 0.5rem;
}
input:focus{
    border: none;
    border: 0.1 solid rgb(149, 193, 240);
    border-radius: 1rem;
}

select {
  font-size: 2.5rem;
  padding: 0.5rem;
  width: 15rem;
  border-radius: 1rem;
  margin-right: 1rem;
}
.results{
    text-align: center;
}

button{
  color: white;
  font-size: 3rem;
  border: none;
  padding: 1rem;
  border-radius: 1rem;
  margin-top: 1rem;
  background-color: rgb(56, 218, 56);
}
button:hover{
  cursor: pointer;
}

i{
  color:red;
}